// initial state
const state = () => ({
  appConfig: {
    /**
     * 
     * @type {String}
     */
    url: "",

    /**
     * ID
     *  documentId 
     * @type {String}
     */
    documentId: "",

    /**
     * 
     * @type {Boolean}
     */
    readOnly: false,
  },
});

// getters
const getters = {};

// actions
const actions = {};

// mutations
const mutations = {
  setDocumentId(state, documentId) {
    state.appConfig.documentId = documentId;
  },
  setUrl(state, url) {
    state.appConfig.url = url;
  },
  setReadOnly(state, readOnly) {
    state.appConfig.readOnly = readOnly;
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
