import EVENT from "@/constants/event";
import { convertThumb } from "@/utils";

/**
 *  app.js webViewer  WebViewer 
 * initialized === webViewerInitialized
 */
class WebViewer {
  constructor(app) {
    this.app = app;
    this.eventBus = app.eventBus;
  }

  /**
   * 
   */
  zoomIn() {
    this.app.zoomIn();
  }

  /**
   * 
   */
  zoomOut() {
    this.app.zoomOut();
  }

  /**
   * 
   */
  scaleChanged(pageScaleValue) {
    this.app.pdfViewer.currentScaleValue = pageScaleValue;
  }

  /**
   * 
   */
  scaleChanging(evt) {
    this.eventBus.dispatch(EVENT.PDF.SET_PAGE_SCALE, {
      pageScaleValue: evt.presetValue,
      pageScale: evt.scale,
    });
    this.app.pdfViewer.update();
  }

  /**
   * 
   */
  showLoadingBar() {
    this.eventBus.dispatch(EVENT.PDF.SHOW_LOADING_BAR);
  }

  /**
   * 
   */
  hideLoadingBar() {
    this.eventBus.dispatch(EVENT.PDF.HIDE_LOADING_BAR);
  }

  /**
   * 
   */
  updateLoadingBar(percent) {
    this.eventBus.dispatch(EVENT.PDF.UPDATE_LOADING_BAR, percent);
  }

  /**
   * 
   */
  previousPage() {
    this.app.pdfViewer.previousPage();
  }

  /**
   * 
   */
  nextPage() {
    this.app.pdfViewer.nextPage();
  }

  /**
   * 
   */
  firstPage() {
    if (this.app.pdfDocument) {
      this.app.page = 1;
    }
  }

  /**
   * 
   */
  lastPage() {
    if (this.app.pdfDocument) {
      this.app.page = this.app.pagesCount;
    }
  }

  /**
   * 
   */
  pageChanging(evt) {
    const { pageNumber } = evt;
    this.eventBus.dispatch(EVENT.PDF.SET_PAGE_NUMBER, {
      pageNumber: pageNumber,
    });
    this.app.pdfThumbnailViewer?.scrollThumbnailIntoView(pageNumber);
  }

  pageNumberChanged(pageNumber) {
    const pdfViewer = this.app.pdfViewer;
    // Note that for `<input type="number">` HTML elements, an empty string will
    // be returned for non-number inputs; hence we simply do nothing in that case.
    if (pageNumber !== "") {
      this.app.pdfLinkService.goToPage(pageNumber);
    }

    // Ensure that the page number input displays the correct value, even if the
    // value entered by the user was invalid (e.g. a floating point number).
    if (pageNumber !== pdfViewer.currentPageNumber.toString()) {
      this.pageChanging({ pageNumber: pdfViewer.currentPageNumber });
    }
  }

  /**
   *  PageController 
   */
  setPagesCount(evt) {
    this.eventBus.dispatch(EVENT.PDF.SET_PAGES_COUNT, {
      pagesCount: evt.pagesCount,
    });
  }

  /**
   * 
   */
  fileInputChange(evt) {
    const file = evt.fileInput.files[0];

    let url = URL.createObjectURL(file);
    if (file.name) {
      url = { url, originalUrl: file.name };
    }

    this.app.open(url);
  }

  /**
   * 
   */
  initialized() {
    const file = this.app.defaultUrl;

    if (file) {
      try {
        this.app.open(file);
      } catch (reason) {
        this.app.l10n.get("loading_error").then((msg) => {
          this.app._documentError(msg, reason);
        });
      }
    }
  }

  /**
   * 
   */
  rotatePage(delta) {
    if (this.app.pdfDocument) {
      this.app.pdfViewer.pagesRotation += delta;
    }
  }

  /**
   * 
   */
  switchSpreadMode(mode) {
    if (this.app.pdfDocument) {
      this.app.pdfViewer.spreadMode = mode;
    }
  }

  /**
   * 
   */
  switchScrollMode(mode) {
    if (this.app.pdfDocument) {
      this.app.pdfViewer.scrollMode = mode;
    }
  }

  /**
   * 
   */
  updateFindMatchesCount({ matchesCount }) {
    this.eventBus.dispatch(EVENT.PDF.FIND_MATCHES_COUNT, matchesCount);
  }

  /**
   * 
   */
  documentLoaded() {
    this.eventBus.dispatch(EVENT.PDF.DOCUMENT_LOADED);
    this.subscribeThumbnailView();
  }

  /**
   * 
   */
  subscribeThumbnailView() {
    if (this.app.pdfSidebar) {
      return
    }

    const { pagesCount, pdfDocument } = this.app;
    const promises = [];
    for (let i = 0; i < pagesCount; i++) {
      promises.push(pdfDocument.getPage(i + 1).then(convertThumb));
    }

    Promise.all(promises).then((thumbnails) => {
      console.log("thumbnails", thumbnails);
      this.eventBus.dispatch(EVENT.PDF.SUBSCRIBE_THUMBNAIL_VIEW, thumbnails);
    });
  }

  /**
   * 
   */
  updateThumbnailView({ pageNumber }) {
    if (this.app.pdfSidebar) {
      return
    }

    const query = `[data-page-number="${pageNumber}"]`;
    const canvas = document.querySelector(query).querySelector('canvas');
    const thumbnail = canvas.toDataURL();
    console.log('', pageNumber)
    this.eventBus.dispatch(EVENT.PDF.UPDATE_THUMBNAIL_VIEW, thumbnail);
  }

  /**
   * 
   */
  subscribeOutline(outline) {
    if (this.app.pdfSidebar) {
      return
    }

    this.eventBus.dispatch(EVENT.PDF.SUBSCRIBE_OUTLINE, outline);
  }

  /**
   * 
   */
  subscribeAttachment(attachments) {
    if (this.app.pdfSidebar) {
      return
    }

    this.eventBus.dispatch(EVENT.PDF.SUBSCRIBE_ATTACHMENT, attachments);
  }

  /**
   * 
   */
  thumbnailViewBindLink(pageNumber) {
    this.app.pdfLinkService.goToPage(pageNumber);
  }

  /**
   * 
   */
  outlineBindLink(dest) {
    if (!dest) {
      return;
    }
    this.app.pdfLinkService.goToDestination(dest);
  }

  /**
   * 
   */
  attachmentBindLink({ content, filename }) {
    const element = document.createElement("a");
    element.innerHTML = filename;
    this.app.downloadManager.openOrDownloadData(element, content, filename);
  }
}

export default WebViewer;
